import React, { useState } from "react";
import { homeWorkReducer } from "./bll/homeWorkReducer";
import SuperButton from "../h4/common/c2-SuperButton/SuperButton";
import s from './HW8.module.css'
import style from '../../p1-main/m1-ui/u1-app/App.module.css'

export type PeopleType = {
    _id: number
    name: string
    age: number
}
export type ActionType = {
    type: "sort" | "check"
    payload: "up" | "down" | number
}

export const initialPeople: Array<PeopleType> = [
    { _id: 0, name: "Кот", age: 3 },
    { _id: 1, name: "Александр", age: 66 },
    { _id: 2, name: "Коля", age: 16 },
    { _id: 3, name: "Виктор", age: 44 },
    { _id: 4, name: "Дмитрий", age: 40 },
    { _id: 5, name: "Ирина", age: 55 },
]

function HW8() {
    const [people, setPeople] = useState(initialPeople);

    const finalPeople = people.map(p => (
        <div className={s.list} key={p._id}>
            <span>{p.name}</span><span>{p.age}</span>
        </div>
    ))


    const sortUp = () => setPeople(homeWorkReducer(initialPeople, { type: "sort", payload: "up" }))
    const sortDown = () => setPeople(homeWorkReducer(initialPeople, { type: "sort", payload: "down" }))
    const check = () => setPeople(homeWorkReducer(initialPeople, { type: "check", payload: 18 }))

    return (
        <div>
            <hr />
            <span className={style.hw}>#HW8</span>

            {/*should work (должно работать)*/}
            <div className={s.box}>
                <div className={s.lists}>
                    {finalPeople}
                </div>
                <div className={s.buttons}>
                    <div><SuperButton onClick={sortUp}>sort up</SuperButton></div>
                    <div><SuperButton onClick={sortDown}>sort down</SuperButton></div>
                    <div><SuperButton onClick={check}>check 18</SuperButton></div>
                </div>
            </div>


            <hr />
            {/*для личного творчества, могу проверить*/}
            {/*<AlternativePeople/>*/}
            <hr />
        </div>
    );
}

export default HW8;
